.resume-btn {
  padding: 0.8em 1.8em;
  border: 1px solid #149eca;
  position: relative;
  overflow: hidden;
  background-color: transparent;
  text-align: center;
  /* text-transform: uppercase; */
  font-size: 16px;
  transition: 0.3s;
  z-index: 1;
  font-family: inherit;
  color: #149eca;
  margin-top: 20px;
  cursor: pointer;
}

.resume-btn::before {
  content: "";
  width: 0;
  height: 300%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: #149eca;
  transition: 0.5s ease;
  display: block;
  z-index: -1;
}

.resume-btn:hover {
  color: white;
}

.resume-btn:hover::before {
  width: 105%;
}
